<template>
  <div>
    <b-row>
      <b-col cols="12" xl="2"></b-col>
      <b-col cols="12" xl="10">
        <label class="title-fw title-fs"
          >{{ $t("CURRENTPOINTSTAGE") }}：エクセレント</label
        >
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="12" xl="2"></b-col>
      <b-col cols="12" xl="4">
        <label class="title-fw title-fs"
          >{{ $t("CURRENTEXCELLENTPOINT") }}：1000p</label
        >
      </b-col>
      <b-col cols="12" xl="6">
        <b-link
          @click="showPointExplanationModal"
          href="javascript:;"
          class="link d-flex align-items-end"
        >
          <u>{{ $t("SEEPOINTDESCRIPTION") }}</u>
        </b-link>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="12" xl="2"></b-col>
      <b-col cols="12" xl="4">
        <label class="title-fw title-fs"
          >{{ $t("POINTSEARNEDTHISMONTH") }}：140p</label
        >
      </b-col>
      <b-col cols="12" xl="6">
        <b-link
          @click="gotoHistory"
          href="javascript:;"
          class="link d-flex align-items-end"
        >
          <u>{{ $t("VIEWPOINTACQUISITIONCONSUMPTIONHISTORY") }}</u>
        </b-link>
      </b-col>
    </b-row>
    <hr class="hrBarType" />

    <b-row>
      <b-col cols="4" xl="1">
        <img
          class="navbar-brand-full p-3"
          src="@/assets/logo.svg"
          width="100%"
        />
      </b-col>
      <b-col cols="8" xl="4">
        <label class="title-fw title-fs-2">カテゴリ1111111</label><br />
        <label class="title-fw title-fs-2">{{ $t("PROGRAMLIST") }}</label>
      </b-col>
    </b-row>
    <!-- カテゴリ一覧 -->
    <b-row>
      <b-col cols="12" xl="1"></b-col>
      <b-col cols="12" xl="10">
        <b-row class="overflow-y">
          <b-col
            v-for="program in categories"
            :key="program.program_no"
            cols="12"
            xl="3"
            no-body
          >
            <img
              v-bind:src="program.program_img_path"
              class="navbar-brand-full p-3"
              width="100%"
            />
            <label>{{ program.program_name }} </label><br />
            <label
              >{{ $t("CONSUMPTION") }}{{ program.consumption_points }}
            </label>
            <b-link
              @click="gotoPointExchangeConfirmation"
              href="javascript:;"
              class="link d-flex align-items-end float-right"
            >
              <u>{{ $t("APPLY") }}</u>
            </b-link>
          </b-col>
        </b-row>
      </b-col>
      <b-col cols="12" xl="1"></b-col>
    </b-row>
    <!-- ページャー＆表示件数 -->
    <br />
    <b-row>
      <b-col cols="12" xl="3"></b-col>
      <b-col cols="12" xl="5">
        <b-pagination
          id="message-list"
          v-if="categories.length != 0"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          align="center"
        ></b-pagination>
      </b-col>
      <b-col cols="12" xl="3">
        <div class="countList" align="right">
          <label>{{ $t("DISPLAY") }}</label
          >&nbsp;&nbsp;
          <label>50 | </label>
          <b-link href="javascript:;">100 | </b-link>
          <b-link href="javascript:;">150</b-link>
        </div>
      </b-col>
      <b-col cols="12" xl="1"></b-col>
    </b-row>

    <!-- CRF-50 ポイント説明 -->
    <b-modal
      id="point-explanation-modal"
      ref="point-explanation-modal"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="xl"
      footer-class="border-0"
      header-class="border-0 bg-white"
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fw title-fs text-dark">
          {{ $t("POINTEXPLANATION") }}
        </div>
        <div class="d-flex justify-content-end">
          <button
            @click="cancel()"
            class="cross-close-btn border-dark text-dark font-weight-bold text-sm m-0 bg-white"
          >
            X
          </button>
        </div>
      </template>
      <div class="my-0 py-0 px-2">
        <label class="highLightHeaderLabel">
          {{ $t("POINTEXPLANATIONDETAIL.LINE1") }}
        </label>
        <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE2") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE3") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE4") }} <br /><br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE5") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE6") }} <br /><br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE7") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE8") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE9") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE10") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE11") }} <br /><br />

        <label class="highLightHeaderLabel">
          {{ $t("POINTEXPLANATIONDETAIL.LINE12") }}
        </label>
        <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE13") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE14") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE15") }} <br /><br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE16") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE17") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE18") }} <br />
        {{ $t("POINTEXPLANATIONDETAIL.LINE19") }} <br />
        <br />
        <br />
      </div>
      <template v-slot:modal-footer="{ cancel }">
        <div class="w-100">
          <button @click="cancel()" class="btn-sm px-1 float-left btn-primary">
            {{ $t("CLOSE") }}
          </button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      categories: this.getprogramList(),
      currentPage: 1,
      perPage: 2,
    };
  },
  computed: {
    rows() {
      return this.categories.length;
    },
  },
  methods: {
    getprogramList() {
      let categories = [];
      for (let i = 1; i <= 9; i++) {
        let programData = {
          program_no: i,
          program_name: "プログラムA",
          consumption_points: "200P",
          program_img_path: require("@/assets/logo.svg"),
        };
        categories.push(programData);
      }
      return categories;
    },

    showPointExplanationModal() {
      this.$refs["point-explanation-modal"].show();
    },

    gotoHistory() {
      this.$router.push({ name: "point_program_history" });
    },

    gotoPointExchangeConfirmation() {
      this.$router.push({ name: "point_program_point_exchange_confirmation" });
    },
  },
};
</script>

<style>
.hrBarType {
  height: 2px;
  border-width: 10;
  color: gray;
  background-color: gray;
}

.overflow-y {
  height: 250px;
  overflow-y: scroll;
}

.highLightHeaderLabel {
  font-weight: 700 !important;
  padding-top: 5px;
}
</style>